@mixin flexCenter($direction, $style, $justify, $align) {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: $justify;
  align-items: $align;
  list-style-type: $style;
  flex-direction: $direction;
}

$background: rgba(255, 255, 255, 0.3);

.col-md-3 {
  padding: 0 !important;
  margin: 0 !important;
}

.col-md-9 {
  padding: 0 !important;
  margin: 0 !important;
}

.row {
  padding: 0 !important;
  margin: 0 !important;
}

.myPhoto {
  width: 100%;
  height: 100%;
}

.view-container {
  @include flexCenter(row, none, center, center);
  background: url("./images/back.jpg") no-repeat center center fixed;
  min-height: 100vh !important;
}

.content-container {
  padding: 1em 0;
  width: 100%;
  min-height: 100%;
}

.description-container {
  @include flexCenter(row, none, none, none);
}

.header-container {
  background: $background;
    p{
      padding: 3em;
    }
}

.name-container {
  background: rgba(68, 64, 64, 0.8);
  h1 {
    color: white;
    font-family: emoji;
    font-size: 28px;
    text-transform: uppercase;
    padding: 0 0.3em;
    margin: 0;
  }
}

.personal-container {
  background: $background;
  padding: 0 1em;
  min-height: 100%;
  h4 {
    @include flexCenter(column, none, center, center);
  }
  ul {
    @include flexCenter(column, none, flex-start, flex-start);
    padding: 0.2em 0;
    li {
      margin: -1px;
    }
  }
}

.property-circle-container {
  h5 {
    font-size: 18px;
  }
  ul {
    @include flexCenter(row, none, space-between, flex-start);
    li {
      @include flexCenter(row, none, center, center);
    }
  }
}

.skills-background-container {
  background: white;
}

.skills-container {
  padding: 0 0 0.5em 0;
  h4 {
    @include flexCenter(column, none, center, center);
  }
  .skills-border {
    padding: 0.2em 0;
    border-bottom: 2px solid rgb(214, 209, 209);
    border-radius: 2px;
  }
}

.skills-properties-container {
  padding: 0.5em 0;
}

.circle-container {
  padding: 0;
  margin: 0;
  @include flexCenter(row, none, center, center);
  ul {
    @include flexCenter(row, none, center, center);
  }
}

.circle {
  width: 12px;
  height: 12px;
}

.circle-padding {
  padding: 0 0.1em;
}

.footer-container {
  opacity: 80%;
  @include flexCenter(row, none, center, flex-end);
}

@media only screen and (max-width: 770px) {
  .name-container {
    @include flexCenter(row, none, center, center);
  }

  .header-container {
    text-align: center;
    p {
      padding: 1em;
    }
  }
}
